    <view class="page-header">
      <text class="page-title">ÂêàÂùõÊ≥ï‰ºöÊî∂‰ª∂‰ø°ÊÅØÁÆ°ÁêÜ</text>
      <text class="page-desc">ÁÆ°ÁêÜÂêàÂùõÊ≥ï‰ºöÊî∂‰ª∂‰ø°ÊÅØÊ®°ÂùóÈÖçÁΩÆ</text>
    </view>
    
    <!-- Ê®°ÂùóÂºÄÂÖ≥ -->
    <view class="module-switch">
