        <switch :checked="moduleEnabled" @change="onModuleToggle" color="#667eea" />
        <text class="switch-desc">{{ moduleEnabled ? 'Â∑≤ÂêØÁî®' : 'Â∑≤Á¶ÅÁî®' }}</text>
      </view>
      <text class="switch-tip">ÂêØÁî®ÂêéÂâçÂè∞Â∞ÜÊòæÁ§∫Êî∂‰ª∂‰ø°ÊÅØÂ°´ÂÜôÂäüËÉΩ</text>
    </view>
    
    <!-- Ê®°ÂùóÂêØÁî®Êó∂ÁöÑÈÖçÁΩÆ -->
