      <view class="config-card">
        <view class="config-header">
          <text class="config-title">Êî∂‰ª∂‰ø°ÊÅØÈÖçÁΩÆ</text>
        </view>
        
        <view class="config-content">
          <view class="config-item">
            <text class="config-label">Êî∂‰ª∂‰∫∫ÂßìÂêç</text>
            <switch :checked="config.nameRequired" @change="onNameRequiredChange" color="#667eea" />
            <text class="config-desc">{{ config.nameRequired ? 'ÂøÖÂ°´' : 'ÈÄâÂ°´' }}</text>
          </view>
          
          <view class="config-item">
            <text class="config-label">Êî∂‰ª∂‰∫∫ÁîµËØù</text>
            <switch :checked="config.phoneRequired" @change="onPhoneRequiredChange" color="#667eea" />
            <text class="config-desc">{{ config.phoneRequired ? 'ÂøÖÂ°´' : 'ÈÄâÂ°´' }}</text>
          </view>
          
          <view class="config-item">
