<script>
export default {
  data() {
    return {
      moduleEnabled: false,
      config: {
        nameRequired: true,
        phoneRequired: true,
        addressRequired: true
      },
      receivers: []
    }
  },
  
  onLoad() {
    this.getConfig()
    this.getList()
  },
  
  methods: {
    async getConfig() {
      try {
        const res = await uniCloud.callFunction({
          name: 'getJointReceiverConfig'
        })
        if (res.result && res.result.data) {
          this.moduleEnabled = res.result.data.enabled || false
          this.config = {
            nameRequired: res.result.data.nameRequired !== false,
            phoneRequired: res.result.data.phoneRequired !== false,
            addressRequired: res.result.data.addressRequired !== false
          }
        }
      } catch (error) {
        console.error('Ëé∑ÂèñÂêàÂùõÊ≥ï‰ºöÊî∂‰ª∂‰ø°ÊÅØÈÖçÁΩÆÂ§±Ë¥•:', error)
        uni.showToast({ title: 'Âä†ËΩΩÈÖçÁΩÆÂ§±Ë¥•', icon: 'none' })
      }
    },
    
    async onModuleToggle(e) {
      try {
        await uniCloud.callFunction({
          name: 'updateJointReceiverConfig',
          data: { 
            enabled: e.detail.value,
            nameRequired: this.config.nameRequired,
            phoneRequired: this.config.phoneRequired,
            addressRequired: this.config.addressRequired
          }
        })
        this.moduleEnabled = e.detail.value
        uni.showToast({ title: e.detail.value ? 'Ê®°ÂùóÂ∑≤ÂêØÁî®' : 'Ê®°ÂùóÂ∑≤Á¶ÅÁî®' })
      } catch (error) {
        console.error('Êõ¥Êñ∞Ê®°ÂùóÁä∂ÊÄÅÂ§±Ë¥•:', error)
        uni.showToast({ title: 'Êìç‰ΩúÂ§±Ë¥•', icon: 'none' })
      }
    },
    
    async onNameRequiredChange(e) {
      this.config.nameRequired = e.detail.value
      await this.saveConfig()
    },
    
    async onPhoneRequiredChange(e) {
      this.config.phoneRequired = e.detail.value
      await this.saveConfig()
    },
    
    async onAddressRequiredChange(e) {
      this.config.addressRequired = e.detail.value
      await this.saveConfig()
    },
    
    async saveConfig() {
      try {
        await uniCloud.callFunction({
          name: 'updateJointReceiverConfig',
          data: { 
            enabled: this.moduleEnabled,
            nameRequired: this.config.nameRequired,
            phoneRequired: this.config.phoneRequired,
            addressRequired: this.config.addressRequired
          }
        })
        uni.showToast({ title: 'ÈÖçÁΩÆÂ∑≤‰øùÂ≠ò' })
      } catch (error) {
        console.error('‰øùÂ≠òÈÖçÁΩÆÂ§±Ë¥•:', error)
        uni.showToast({ title: '‰øùÂ≠òÂ§±Ë¥•', icon: 'none' })
      }
    },
    
    async getList() {
      try {
        const res = await uniCloud.callFunction({
          name: 'getJointReceivers'
        })
        this.receivers = res.result && res.result.data ? res.result.data : []
      } catch (error) {
        console.error('Ëé∑ÂèñÂêàÂùõÊ≥ï‰ºöÊî∂‰ª∂‰ø°ÊÅØÂàóË°®Â§±Ë¥•:', error)
        uni.showToast({ title: 'Âä†ËΩΩÂ§±Ë¥•', icon: 'none' })
      }
    },
    
    copyReceiverInfo(receiver) {
      const info = `Êî∂‰ª∂‰∫∫Ôºö${receiver.name}\nÁîµËØùÔºö${receiver.phone}\nÂú∞ÂùÄÔºö${receiver.address}`
      uni.setClipboardData({
        data: info,
        success: () => {
          uni.showToast({ title: '‰ø°ÊÅØÂ∑≤Â§çÂà∂' })
        }
      })
    },
    
    async deleteReceiver(id) {
      uni.showModal({
        title: 'Á°ÆËÆ§Âà†Èô§',
        content: 'Á°ÆÂÆöË¶ÅÂà†Èô§Ëøô‰∏™Êî∂‰ª∂‰ø°ÊÅØÂêóÔºü',
        success: async (res) => {
          if (res.confirm) {
            try {
              await uniCloud.callFunction({
                name: 'deleteJointReceiver',
                data: { id }
              })
              await this.getList()
              uni.showToast({ title: 'Âà†Èô§ÊàêÂäü' })
            } catch (error) {
              console.error('Âà†Èô§Êî∂‰ª∂‰ø°ÊÅØÂ§±Ë¥•:', error)
              uni.showToast({ title: 'Âà†Èô§Â§±Ë¥•', icon: 'none' })
            }
          }
        }
      })
    },
    
    formatTime(time) {
      if (!time) return ''
      const date = new Date(time)
      return `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(2, '0')}-${String(date.getDate()).padStart(2, '0')} ${String(date.getHours()).padStart(2, '0')}:${String(date.getMinutes()).padStart(2, '0')}`
    }
  }
}
</script>

